// --- Helper Functions ---
const formatTime = (timeInSeconds: number): string => {
  const minutes = Math.floor(timeInSeconds / 60);
  const seconds = Math.floor(timeInSeconds % 60);
  return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
};

// --- Components ---

// --- FloatingProgressRing ---
interface FloatingProgressRingProps {
  progress: number; // 0-100
}
const FloatingProgressRing: React.FC<FloatingProgressRingProps> = ({ progress }) => {
  const radius = 16;
  const circumference = 2 * Math.PI * radius;
  const offset = circumference - (progress / 100) * circumference;

  return (
    <div className={`fixed bottom-4 right-4 z-50 p-2 rounded-full ${glassmorphismStyle('bg-[#FFA896]', 'border-[#CD1C18]/70')}`}>
      <svg className="w-12 h-12 transform -rotate-90" viewBox="0 0 36 36">
        <circle
          cx="18" cy="18" r={radius}
          className="text-[#3B000A]/30"
          strokeWidth="3" fill="transparent"
        />
        <circle
          cx="18" cy="18" r={radius}
          className="text-[#CD1C18]"
          strokeWidth="3" fill="transparent"
          strokeDasharray={circumference}
          strokeDashoffset={offset}
          strokeLinecap="round"
        />
      </svg>
      <span className="absolute inset-0 flex items-center justify-center text-xs font-semibold text-[#3B000A]">
        {Math.round(progress)}%
      </span>
    </div>
  );
};

// --- DraggableAttendanceChecklist ---
interface DraggableAttendanceChecklistProps {
  attendees: Attendee[];
  onTogglePresent: (id: string) => void;
}
const DraggableAttendanceChecklist: React.FC<DraggableAttendanceChecklistProps> = ({ attendees, onTogglePresent }) => {
  const [position, setPosition] = useState({ x: 20, y: 200 }); // Initial position
  const [isDragging, setIsDragging] = useState(false);
  const dragRef = useRef<HTMLDivElement>(null);
  const offsetRef = useRef({ x: 0, y: 0 });

  const handleMouseDown = (e: React.MouseEvent<HTMLDivElement>) => {
    if (dragRef.current) {
      setIsDragging(true);
      offsetRef.current = {
        x: e.clientX - dragRef.current.getBoundingClientRect().left,
        y: e.clientY - dragRef.current.getBoundingClientRect().top,
      };
      document.body.style.userSelect = 'none'; // Prevent text selection
    }
  };

  const handleTouchStart = (e: React.TouchEvent<HTMLDivElement>) => {
    if (dragRef.current) {
      setIsDragging(true);
      const touch = e.touches[0];
      offsetRef.current = {
        x: touch.clientX - dragRef.current.getBoundingClientRect().left,
        y: touch.clientY - dragRef.current.getBoundingClientRect().top,
      };
      // No user-select for touch typically needed, but good to be aware
    }
  };
  
  const handleMouseMove = useCallback((e: MouseEvent) => {
    if (!isDragging) return;
    setPosition({
      x: e.clientX - offsetRef.current.x,
      y: e.clientY - offsetRef.current.y,
    });
  }, [isDragging]);

  const handleTouchMove = useCallback((e: TouchEvent) => {
    if (!isDragging || e.touches.length === 0) return;
    const touch = e.touches[0];
    setPosition({
      x: touch.clientX - offsetRef.current.x,
      y: touch.clientY - offsetRef.current.y,
    });
  }, [isDragging]);

  const handleMouseUp = () => {
    setIsDragging(false);
    document.body.style.userSelect = '';
  };
  
  const handleTouchEnd = () => {
    setIsDragging(false);
  };

  useEffect(() => {
    if (isDragging) {
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
      document.addEventListener('touchmove', handleTouchMove);
      document.addEventListener('touchend', handleTouchEnd);
    } else {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
      document.removeEventListener('touchmove', handleTouchMove);
      document.removeEventListener('touchend', handleTouchEnd);
    }
    return () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
      document.removeEventListener('touchmove', handleTouchMove);
      document.removeEventListener('touchend', handleTouchEnd);
      document.body.style.userSelect = '';
    };
  }, [isDragging, handleMouseMove, handleTouchMove]);

  return (
    <div
      ref={dragRef}
      className={`fixed z-40 p-4 rounded-xl shadow-2xl ${glassmorphismStyle('bg-[#FFA896]', 'border-[#CD1C18]/70', 'text-[#3B000A]')}`}
      style={{ top: `${position.y}px`, left: `${position.x}px`, touchAction: 'none' }} // touchAction: 'none' on draggable element
    >
      <div 
        className="cursor-move pb-2 mb-2 border-b border-[#CD1C18]/50 text-sm font-semibold"
        onMouseDown={handleMouseDown}
        onTouchStart={handleTouchStart}
      >
        Attendance (Drag Me)
      </div>
      <ul className="space-y-1 max-h-48 overflow-y-auto pr-2"> {/* Added pr-2 for scrollbar space */}
        {attendees.map(attendee => (
          <li key={attendee.id} className="flex items-center justify-between text-xs">
            <span>{attendee.name}</span>
            <button onClick={() => onTogglePresent(attendee.id)} className="ml-2 p-1">
              {attendee.present ? <CheckCircleIcon className="w-4 h-4 text-[#CD1C18]" /> : <CircleIcon className="w-4 h-4 text-[#3B000A]/50" />}
            </button>
          </li>
        ))}
      </ul>
       {/* Note: For very long lists, consider virtualization (e.g., react-window) for performance. */}
    </div>
  );
};


