// --- AnnotationItem ---
interface AnnotationItemProps {
  annotation: AnnotationItemType;
  isCurrent: boolean; // Highlight if video time is near this annotation
  onTimestampClick: (timestamp: number) => void;
}
const AnnotationItem: React.FC<AnnotationItemProps> = ({ annotation, isCurrent, onTimestampClick }) => {
  const [showComments, setShowComments] = useState(false);
  const [newComment, setNewComment] = useState('');

  const handleAddComment = (e: React.FormEvent) => {
    e.preventDefault();
    if (newComment.trim()) {
      // In a real app, this would call a prop function to update state
      console.log(`New comment for annotation ${annotation.id}: ${newComment} by User`);
      annotation.comments.push({ id: Date.now().toString(), user: 'You', text: newComment });
      setNewComment('');
    }
  };

  return (
    <div className={`p-3 mb-3 rounded-lg transition-all duration-300 ${isCurrent ? 'bg-[#CD1C18]/40' : 'bg-[#FFA896]/20'} border border-[#9B1313]/30`}>
      <div className="flex justify-between items-start">
        <div>
          <span className="text-xs text-[#3B000A] font-medium">{annotation.user}</span>
          <p className="text-sm text-neutral-900 my-1">{annotation.text}</p>
        </div>
        <button 
          onClick={() => onTimestampClick(annotation.timestamp)}
          className="text-xs bg-[#CD1C18] text-[#F9F9F9] px-2 py-1 rounded hover:bg-[#9B1313] transition-colors"
        >
          {formatTime(annotation.timestamp)}
        </button>
      </div>
      
      <div className="mt-2">
        <button 
          onClick={() => setShowComments(!showComments)}
          className="text-xs text-[#CD1C18] hover:underline"
        >
          {showComments ? 'Hide' : 'Show'} {annotation.comments.length} comments
        </button>
        {showComments && (
          <div className="mt-2 space-y-2 pl-2 border-l-2 border-[#CD1C18]/50 max-h-32 overflow-y-auto">
            {/* Note: For many comments, use virtualization (e.g., react-window) */}
            {annotation.comments.map(comment => (
              <div key={comment.id} className="text-xs bg-[#F9F9F9]/50 p-1.5 rounded">
                <span className="font-semibold text-[#3B000A]">{comment.user}: </span>
                <span className="text-neutral-900">{comment.text}</span>
              </div>
            ))}
            <form onSubmit={handleAddComment} className="flex items-center mt-2">
              <input 
                type="text"
                value={newComment}
                onChange={(e) => setNewComment(e.target.value)}
                placeholder="Add a comment..."
                className="flex-grow p-1.5 text-xs border border-[#9B1313]/30 rounded-l-md focus:ring-1 focus:ring-[#CD1C18] focus:outline-none text-neutral-900 bg-white/70 placeholder:text-[#3B000A]/70"
              />
              <button type="submit" className="p-1.5 bg-[#CD1C18] text-[#F9F9F9] text-xs rounded-r-md hover:bg-[#9B1313] transition-colors">
                Send
              </button>
            </form>
          </div>
        )}
      </div>
    </div>
  );
};

// --- AnnotationSidebar ---
interface AnnotationSidebarProps {
  isOpen: boolean;
  onClose: () => void;
  annotations: AnnotationItemType[];
  currentTime: number;
  onAddAnnotation: (text: string, timestamp: number) => void;
  onTimestampClick: (timestamp: number) => void; // To seek video
}
const AnnotationSidebar: React.FC<AnnotationSidebarProps> = ({ isOpen, onClose, annotations, currentTime, onAddAnnotation, onTimestampClick }) => {
  const [newAnnotationText, setNewAnnotationText] = useState('');
  const sidebarRef = useRef<HTMLDivElement>(null);

  const handleAddAnnotation = (e: React.FormEvent) => {
    e.preventDefault();
    if (newAnnotationText.trim()) {
      onAddAnnotation(newAnnotationText, currentTime);
      setNewAnnotationText('');
    }
  };
  
  // Gesture handling for closing sidebar (swipe right)
  const touchStartX = useRef(0);
  const handleTouchStart = (e: React.TouchEvent) => {
    touchStartX.current = e.targetTouches[0].clientX;
  };
  const handleTouchMove = (e: React.TouchEvent) => {
    if (!isOpen) return;
    const touchCurrentX = e.targetTouches[0].clientX;
    if (touchCurrentX - touchStartX.current > 50) { // Swipe right threshold
      onClose();
    }
  };

  const sortedAnnotations = useMemo(() => 
    [...annotations].sort((a, b) => a.timestamp - b.timestamp), 
  [annotations]);

  return (
    <div
      ref={sidebarRef}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      className={`fixed top-0 right-0 h-full w-full max-w-md transform transition-transform duration-300 ease-in-out z-50 flex flex-col
                  ${isOpen ? 'translate-x-0' : 'translate-x-full'}
                  ${glassmorphismStyle('bg-[#FFA896]', 'border-l border-[#CD1C18]/70', 'text-[#3B000A]')}`}
    >
      <div className="flex items-center justify-between p-4 border-b border-[#CD1C18]/50">
        <h2 className="text-lg font-semibold text-[#3B000A]">Annotations</h2>
        <button onClick={onClose} className="p-1 text-[#3B000A] hover:text-[#CD1C18]">
          <CloseIcon />
        </button>
      </div>

      <div className="flex-grow p-4 overflow-y-auto space-y-2">
        {sortedAnnotations.map(anno => (
          <AnnotationItem 
            key={anno.id} 
            annotation={anno}
            isCurrent={Math.abs(currentTime - anno.timestamp) < 5} // Highlight if within 5s
            onTimestampClick={onTimestampClick}
          />
        ))}
         {/* Note: For very long lists of annotations, consider virtualization (e.g., react-window) */}
      </div>

      <form onSubmit={handleAddAnnotation} className="p-4 border-t border-[#CD1C18]/50">
        <textarea
          value={newAnnotationText}
          onChange={(e) => setNewAnnotationText(e.target.value)}
          placeholder={`Add annotation at ${formatTime(currentTime)}...`}
          rows={3}
          className="w-full p-2 text-sm border border-[#9B1313]/30 rounded-md focus:ring-1 focus:ring-[#CD1C18] focus:outline-none mb-2 text-neutral-900 bg-white/70 placeholder:text-[#3B000A]/70"
        />
        <button type="submit" className="w-full p-2 bg-[#CD1C18] text-[#F9F9F9] rounded-md hover:bg-[#9B1313] transition-colors">
          Add Annotation
        </button>
      </form>
    </div>
  );
};


// --- VideoPlayer ---
interface VideoPlayerProps {
  currentTime: number;
  duration: number;
  isPlaying: boolean;
  onPlayPause: () => void;
  onSeek: (time: number) => void;
  title: string;
}
const VideoPlayer: React.FC<VideoPlayerProps> = ({ currentTime, duration, isPlaying, onPlayPause, onSeek, title }) => {
  const progressPercent = duration > 0 ? (currentTime / duration) * 100 : 0;

  const handleSeek = (event: React.ChangeEvent<HTMLInputElement>) => {
    onSeek(Number(event.target.value));
  };

  return (
    <div className="w-full aspect-video bg-[#3B000A] text-[#F9F9F9] flex flex-col justify-between shadow-2xl rounded-b-xl overflow-hidden">
      {/* Mock Video Screen */}
      <div className="flex-grow flex items-center justify-center relative">
        <p className="text-2xl font-semibold opacity-80 select-none">{title}</p>
        <div className="absolute top-2 left-2 px-2 py-1 bg-black/30 rounded text-xs">Mock Video Player</div>
      </div>

      {/* Controls */}
      <div className={`p-3 ${glassmorphismStyle('bg-neutral-900', 'border-t border-[#CD1C18]/50', 'text-[#F9F9F9]')}`}>
        <div className="flex items-center space-x-3">
          <button onClick={onPlayPause} className="p-1.5 text-[#F9F9F9] hover:text-[#FFA896]">
            {isPlaying ? <PauseIcon className="w-7 h-7"/> : <PlayIcon className="w-7 h-7"/>}
          </button>
          <span className="text-xs w-10 text-center">{formatTime(currentTime)}</span>
          <input
            type="range"
            min="0"
            max={duration}
            value={currentTime}
            onChange={handleSeek}
            className="flex-grow h-2 rounded-lg appearance-none cursor-pointer bg-[#F9F9F9]/30 [&::-webkit-slider-thumb]:appearance-none [&::-webkit-slider-thumb]:w-3.5 [&::-webkit-slider-thumb]:h-3.5 [&::-webkit-slider-thumb]:rounded-full [&::-webkit-slider-thumb]:bg-[#CD1C18]"
            style={{ backgroundSize: `${progressPercent}% 100%` }} // This might need more custom CSS for full cross-browser track fill
          />
          <span className="text-xs w-10 text-center">{formatTime(duration)}</span>
        </div>
      </div>
    </div>
  );
};

// --- Main App ---
const StudyGroupApp: React.FC = () => {
  const [currentTime, setCurrentTime] = useState(0);
  const [duration] = useState(300); // 5 minutes mock duration
  const [isPlaying, setIsPlaying] = useState(false);
  const [annotations, setAnnotations] = useState<AnnotationItemType[]>(MOCK_ANNOTATIONS);
  const [attendees, setAttendees] = useState<Attendee[]>(MOCK_ATTENDEES);
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  
  // Video playback simulation
  useEffect(() => {
    let interval: NodeJS.Timeout;
    if (isPlaying && currentTime < duration) {
      interval = setInterval(() => {
        setCurrentTime(prevTime => Math.min(prevTime + 1, duration));
      }, 1000);
    } else if (currentTime >= duration) {
      setIsPlaying(false);
    }
    return () => clearInterval(interval);
  }, [isPlaying, currentTime, duration]);

  const handlePlayPause = () => setIsPlaying(!isPlaying);
  const handleSeek = (time: number) => {
    setCurrentTime(Math.max(0, Math.min(time, duration)));
  };

  const handleAddAnnotation = (text: string, timestamp: number) => {
    const newAnnotation: AnnotationItemType = {
      id: `anno-${Date.now()}`,
      timestamp,
      user: 'You', // Or get from auth context
      text,
      comments: [],
    };
    setAnnotations(prev => [...prev, newAnnotation]);
  };

  const handleTogglePresent = (id: string) => {
    setAttendees(prev =>
      prev.map(a => (a.id === id ? { ...a, present: !a.present } : a))
    );
  };

  const handleEmojiSubmit = (emojiId: string) => {
    const selectedEmoji = EMOJI_PULSE_OPTIONS.find(o => o.id === emojiId);
    console.log(`Pulse received: ${selectedEmoji?.emoji} ${selectedEmoji?.label}`);
    // Here you would typically send this to a backend
  };

  const videoProgress = duration > 0 ? (currentTime / duration) * 100 : 0;

  // Gesture handling for opening sidebar (swipe left on main content)
  const mainContentRef = useRef<HTMLDivElement>(null);
  const touchStartX = useRef(0);

  const handleTouchStartMain = (e: React.TouchEvent) => {
    // Only trigger if swipe starts outside interactive elements like range inputs
    if ((e.target as HTMLElement).closest('input[type="range"], button, a, textarea, input[type="text"]')) {
      return;
    }
    touchStartX.current = e.targetTouches[0].clientX;
  };

  const handleTouchMoveMain = (e: React.TouchEvent) => {
    if (isSidebarOpen || touchStartX.current === 0) return; // Don't detect if sidebar is open or touch didn't start properly
    const touchCurrentX = e.targetTouches[0].clientX;
    if (touchStartX.current - touchCurrentX > 70) { // Swipe left threshold
      setIsSidebarOpen(true);
      touchStartX.current = 0; // Reset to prevent re-triggering
    }
  };
  const handleTouchEndMain = () => {
    touchStartX.current = 0; // Reset on touch end
  };


  return (
    <div 
      className="h-screen w-screen flex flex-col bg-[#F9F9F9] text-neutral-900 overflow-hidden"
      onTouchStart={handleTouchStartMain}
      onTouchMove={handleTouchMoveMain}
      onTouchEnd={handleTouchEndMain}
      ref={mainContentRef}
    >
      {/* Header */}
      <header className={`p-3 flex items-center justify-between ${glassmorphismStyle('bg-[#CD1C18]', 'border-b border-[#9B1313]/50', 'text-[#F9F9F9]')}`}>
        <h1 className="text-lg font-bold">Mobile Study Group</h1>
        <button onClick={() => setIsSidebarOpen(true)} className="p-1.5 text-[#F9F9F9] hover:text-[#FFA896]">
          <AnnotationIcon />
        </button>
      </header>

      {/* Main Content Area */}
      <div className="flex-grow overflow-y-auto p-1 sm:p-2 md:p-4 space-y-4">
        <VideoPlayer
          currentTime={currentTime}
          duration={duration}
          isPlaying={isPlaying}
          onPlayPause={handlePlayPause}
          onSeek={handleSeek}
          title="Lecture 1: Advanced Spices"
        />
        
        <div className="px-2 sm:px-0"> {/* Add horizontal padding for content inside scroll area */}
            <EmojiPulseSurvey options={EMOJI_PULSE_OPTIONS} onSubmit={handleEmojiSubmit} />
        </div>
        
        {/* Placeholder for other content if needed */}
        {/* <div className="h-32 bg-gray-200 rounded-lg flex items-center justify-center text-gray-500">Other Content Area</div> */}
      </div>

      {/* Floating Components */}
      <DraggableAttendanceChecklist attendees={attendees} onTogglePresent={handleTogglePresent} />
      <FloatingProgressRing progress={videoProgress} />

      {/* Annotation Sidebar */}
      <AnnotationSidebar
        isOpen={isSidebarOpen}
        onClose={() => setIsSidebarOpen(false)}
        annotations={annotations}
        currentTime={currentTime}
        onAddAnnotation={handleAddAnnotation}
        onTimestampClick={(time) => {
            handleSeek(time);
            if (!isPlaying) setIsPlaying(true); // Optionally play when clicking timestamp
        }}
      />
    </div>
  );
};

export default StudyGroupApp;
